.intervention-card-container {
    padding-bottom: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fit, 350px);
    grid-gap: 30px;
  }
  
  .intervention-card {
    background: #fff;
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    width: 350px;
  
    // Override default styling for links
    color: $text-color;
    text-decoration: none;
  
    // Really basic reset
    p,
    h2 {
      margin: 0;
    }
  
    .intervention-card-image {
      overflow: hidden;
      border-radius: 12px 12px 0 0;
      max-height: 260px;
      background-color: $mte-blue;
      //transition: max-height 0.2s ease-in;
  
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        //transition: all 0.2s ease-out;
      }
    }
  
    .intervention-card-info,
    .intervention-card-meta {
      padding-left: 30px;
      padding-right: 30px;
    }
  
    .intervention-card-info {
        padding-top: 30px;
        padding-bottom: 30px;
        flex-grow: 1;

        .category {
            position: absolute;
            margin-top: -55px;
            padding: 5px 15px;
            color: #FFFFFF;
            font-size: 14px;
            font-weight: 600;
            text-transform: uppercase;
            border-radius: 5px;

            &.cognitive {
                background: $accent-green;
            }

            &.communicative {
                background: $mte-pink;
            }

            &.emotional {
                background: $accent-yellow;
            }

            &.musical {
                background: $mte-blue;
            }

            &.psychosocial {
                background: $campfire-orange;
            }

            &.sensorimotor {
                background: $accent-purple;
            }

            &.spiritual {
                background: $accent-magenta;
            }
        }
    
        h2,
        h3 {
            font-size: 1.4em;
            margin: 0;
            color: $mte-blue;
            font-weight: 500;
        }

        .description {
            max-height: 0;
            overflow: hidden;
            color: $text-color;
        }
    
        p {
            margin-top: 10px;
            margin-bottom: 0;
        }

        .intervention-card-taxonomies {
            color: $text-color;
            font-weight: 600;

            ul {
                padding: 0;
                list-style-type: none;
                font-size: 14px;
                line-height: 16px;
            }

            li {
                padding: 10px 0 10px 25px;

                &:before {
                    position:absolute;
                    margin-left: -25px;
                }

                &:first-of-type:before {
                    content: "\f1ae";
                    font-family: 'Font Awesome 5 Pro';
                }

                &:last-of-type:before {
                    content: "\f7a6";
                    font-family: 'Font Awesome 5 Pro';
                }
            }

            li+li {
                padding-top: 0;
            }
        }
    }
  
    .intervention-card-meta {
      padding-bottom: 30px;
      font-weight: bold;
      display: flex;
      flex-direction: row;

      .unlocks {
        flex-grow: 1;
        padding: 8px 0;

        .count {
            background: $accent-yellow;
            color: #ffffff;
            border-radius: 5px;
            padding: 5px 10px;
            font-weight: bold;
            margin-right: 5px;

            i {
                color: #fff;
                margin-right: 5px;
            }
        }
      }
    }
  
    &:hover {
      color: $text-color;

        .intervention-card-info .description {
            max-height: 260px;
            //transition: max-height 0.4s ease-in-out;
        }

        .intervention-card-image {
            max-height: 40px;
            //transition: max-height 0.4s ease-out;

            img {
                opacity: 0.5;
                //transform: scale(1.05);
            }
        }
    }
}
  


/* INTERVENTION intervention-cardS */

.intervention-card-module {
    position: relative;
    z-index: 1;
    display: block;
    background: #FFFFFF;
    width: 320px;
    height: 500px;
    margin: 0px 20px 30px 20px;
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
    transition: all 0.25s ease-out;
    /* Flexbox settings */
    order: 0;
    flex: 0 1 auto;
    align-self: auto;

    &:hover,
    &.hover {
        box-shadow: 0px 1px 35px 0px rgba(0, 0, 0, 0.5);

        .intervention-card-content .description {
            max-height: 350px;
            transition: max-height 0.4s ease-out;
        }
    }

    &:hover .thumbnail img,
    .hover .thumbnail img {
        opacity: 0.5;
    }

    .thumbnail {
        background: #FFFFFF;
        height: 250px;
        overflow: hidden;

        img {
            display: block;
            width: 120%;
            height: 100%;
            object-fit: cover;
            transition: all 0.25s ease-out;
        }
    }

    .intervention-card-content {
        position: absolute;
        bottom: 0;
        background: #FFFFFF;
        width: 100%;
        padding: 30px 30px 20px 30px;
        font-family: 'Open Sans', sans-serif;
        box-sizing: border-box;
        transition: all 0.25s cubic-bezier(0.37, 0.75, 0.61, 1.05) 0s;

        .category {
            position: absolute;
            top: -18px;
            right: 20px;
            padding: 5px 15px;
            color: #FFFFFF;
            font-size: 14px;
            font-weight: 600;
            text-transform: uppercase;
            border-radius: 5px;

            &.cognitive {
                background: $accent-green;
            }

            &.communicative {
                background: $mte-pink;
            }

            &.emotional {
                background: $accent-yellow;
            }

            &.musical {
                background: $mte-blue;
            }

            &.psychosocial {
                background: $campfire-orange;
            }

            &.sensorimotor {
                background: $accent-purple;
            }

            &.spiritual {
                background: $accent-magenta;
            }
        }

        .title {
            margin: 0;
            padding: 0 0 10px;
            color: $black;
            font-size: 26px;
            font-weight: 700;
            font-family: 'Raleway', sans-serif;
        }

        .sub_title {
            margin: 0;
            padding: 0 0 20px;
            color: $campfire-orange;
            font-size: 20px;
            font-weight: 400;
            font-family: 'Raleway', sans-serif;
        }

        .description {
            max-height: 0;
            transition: max-height 0.25s ease-out;
            overflow: hidden;
            color: $text-color;
            font-size: 14px;
            line-height: 1.8em;
        }

        .intervention-card-meta {
            color: $text-color;
            font-weight: 600;

            ul {
                padding: 0;
                list-style-type: none;
                font-size: 14px;
                line-height: 16px;
            }

            li {
                padding: 10px 0 10px 25px;

                &:before {
                    position:absolute;
                    left: 30px;
                }

                &:first-of-type:before {
                    content: "\f1ae";
                    font-family: 'Font Awesome 5 Pro';
                }

                &:last-of-type:before {
                    content: "\f7a6";
                    font-family: 'Font Awesome 5 Pro';
                }

                i {
                    padding-right: 15px;
                }
            }
        }

        .intervention-card-access {
            margin: 10px 0 0;
            text-align: center;

            a {
                text-decoration: none;
            }

            .button {
                color: #FFFFFF;
                font-weight: bold;
                padding: 12px 20px;
                border-radius: 5px;
                box-shadow: 0px 5px 10px -6px rgba(0, 0, 0, 0.3);
                transition: all 0.25s ease-out;
                cursor: pointer;

                &:hover {
                    filter: brightness(110%);
                }

                i {
                    padding-left: 5px;
                }
            }
        }
    }
}

/* Disabled intervention-cards */
.disabled {
    .intervention-card-image img {
        filter: grayscale(100%);
    }

    .intervention-card-content {
        .description,
        .intervention-card-taxonomies  {
            color: $med-gray;
        }

        .title {
            color: $text-color;
        }

        .intervention-card-meta {
            .button,
            .count {
                background: $text-color;
            }
        }

        .category {
            &.cognitive,
            &.communicative,
            &.emotional,
            &.musical,
            &.psychosocial,
            &.sensorimotor,
            &.spiritual {
                background: $text-color;
            }
        }
    }
}